export function addInvestingCriteria(
  ltv_max,
  min_irr,
  investment_min,
  investment_max,
  holding_period_min,
  holding_period_max,
  rate_min,
  rate_max,
  cash_flow_needed,
  nationwide,
  states,
  return_types,
  construction_types,
  property_types,
  fund_types,
  investment_types
) {
  return async (dispatch) => {
    return await ajax({
      url: `${config.BASE_URL}funds/criteria`,
      applyAuthHeader: true,
      data: {
        ltv_max,
        min_irr,
        investment_min,
        investment_max,
        holding_period_min,
        holding_period_max,
        rate_min,
        rate_max,
        cash_flow_needed,
        nationwide,
        states,
        return_types,
        construction_types,
        property_types,
        fund_types,
        investment_types,
      },
      type: "POST",
      success: function (response) {
        dispatch({
          type: types.ADD_INVESTING_CRITERIA,
          success: true,
          message: response.message || "Investing Criteria Added Successfully!",
        });
      },
      error: handleAPIError(dispatch, types.ADD_INVESTING_CRITERIA),
    }).catch((err) => {
      return undefined;
    });
  };
}

export function editInvestingCriteria(
  ltv_max,
  min_irr,
  investment_min,
  investment_max,
  holding_period_min,
  holding_period_max,
  rate_min,
  rate_max,
  cash_flow_needed,
  nationwide,
  states,
  return_types,
  construction_types,
  property_types,
  fund_types,
  investment_types,
  criteria_id
) {
  return async (dispatch) => {
    return await ajax({
      url: `${config.BASE_URL}funds/criteria/${criteria_id}`,
      applyAuthHeader: true,
      data: {
        ltv_max,
        min_irr,
        investment_min,
        investment_max,
        holding_period_min,
        holding_period_max,
        rate_min,
        rate_max,
        cash_flow_needed,
        nationwide,
        states,
        return_types,
        construction_types,
        property_types,
        fund_types,
        investment_types,
      },
      type: "PUT",
      success: function (response) {
        dispatch({
          type: types.EDIT_INVESTING_CRITERIA,
          success: true,
          message: response.message || "Updated Successfully!",
        });
      },
      error: handleAPIError(dispatch, types.EDIT_INVESTING_CRITERIA),
    }).catch((err) => {
      return undefined;
    });
  };
}

export function deleteInvestingCriteria(criteria_id, page) {
  return async (dispatch) => {
    return await ajax({
      url: `${config.BASE_URL}funds/criteria/${criteria_id}`,
      applyAuthHeader: true,
      type: "DELETE",
      success: function (response) {
        dispatch({
          type: types.DELETE_INVESTING_CRITERIA,
          success: true,
          message: "Deleted successfully!",
        });
        dispatch(getInvestingCriteriaList(page));
      },
      error: handleAPIError(dispatch, types.DELETE_INVESTING_CRITERIA),
    }).catch((err) => {
      return undefined;
    });
  };
}

export function closeToast() {
  return { type: types.CLOSE_IC_TOAST };
}
