import React from "react";
import Container from "muicss/lib/react/container";
import { Navigation } from "../../lib";
import InvestingCriteria from "./InvestingCriteria";
import PageNavigation from "../common/PageNavigation";
import encodeQueryData from "../common/encodeQueryData";

class InvestingCriteriaPage extends React.Component {
  addInvestingCriteriaClick() {
    this.props.setPageAndScrollPos(window.location.pathname, window.scrollY);
    Navigation.go(
      `investing-criteria/add${
        window.location.search
          ? `?${encodeQueryData({
              previous_page: window.location.pathname + window.location.search,
            })}`
          : ""
      }`
    );
  }

  render() {
    return (
      <div>
        <div className="lc-header">
          <Container>
            <h2>Investing Profiles</h2>
            <div onClick={() => this.addInvestingCriteriaClick()}>
              <div className="add-circle">
                <i className="material-icons">add</i>
              </div>
            </div>
          </Container>
        </div>
        {this.props.investingCriteriaList &&
        this.props.investingCriteriaList.length > 0 ? (
          <div>
            {this.props.investingCriteriaList.map((item, index) => (
              <InvestingCriteria
                key={index}
                index={parseInt(this.props.from, 10) + index}
                item={item}
                fundTypesList={this.props.fundTypesList}
                returnTypesList={this.props.returnTypesList}
                investmentTypesList={this.props.investmentTypesList}
                propTypesList={this.props.propTypesList}
                constructionTypesList={this.props.constructionTypesList}
                statesList={this.props.statesList}
                delete={this.props.delete}
                currentPage={this.props.currentPage}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
              />
            ))}
          </div>
        ) : (
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">{`No Investing Criteria${
              this.props.currentPage > 1 ? " on this Page" : ""
            }`}</h2>
          </div>
        )}

        <PageNavigation
          currentPage={this.props.currentPage}
          nextPageUrl={this.props.nextPageUrl}
        />
      </div>
    );
  }
}

export default InvestingCriteriaPage;
