import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Divider from "muicss/lib/react/divider";
import { Navigation } from "../../lib";
import { formatCurrency, formatPercentage } from "../../lib";
import ConfirmationModal from "../common/ConfirmationModal";
import encodeQueryData from "../common/encodeQueryData";

class InvestingCriteria extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showDeleteModal: false,
    };
  }

  formatTypesByValue(typesSelected, fullTypesList) {
    let typesValue = "";
    typesSelected &&
      typesSelected.map((value, index) => {
        let type = fullTypesList.find((t) => t.list_type_id === value);
        if (type) {
          if (index >= 1) {
            typesValue += ", ";
          }
          typesValue += type.value;
        }
        return typesValue;
      });
    return typesValue;
  }

  async delete() {
    await this.props.delete(
      this.props.item.inv_crit_id,
      this.props.currentPage
    );
    this.setState({ showDeleteModal: false });
  }

  editInvestingCriteriaClick() {
    this.props.setPageAndScrollPos(window.location.pathname, window.scrollY);
    Navigation.go(
      `investing-criteria/edit/${
        this.props.item.inv_crit_id
      }?${encodeQueryData({
        previous_page: window.location.pathname + window.location.search,
      })}`
    );
  }

  render() {
    return (
      <div>
        <div className="panel mui-panel ic-panel">
          <div className="panel-heading">
            <p className="large-text bold-text">
              Investing Profile {this.props.index}
            </p>
            <div className="flex">
              <div onClick={() => this.editInvestingCriteriaClick()}>
                <i className="material-icons icon-left">edit</i>
              </div>
              <i
                className="material-icons"
                onClick={() => this.setState({ showDeleteModal: true })}
              >
                delete
              </i>
            </div>
          </div>
          <div className="panel-body">
            <Row>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Fund Type</p>
                <p className="panel-info-value">
                  {this.props.item.fund_types
                    ? this.formatTypesByValue(
                        this.props.item.fund_types.map((t) => t.value),
                        this.props.fundTypesList
                      )
                    : ""}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Return Type</p>
                <p className="panel-info-value">
                  {this.props.item.return_types
                    ? this.formatTypesByValue(
                        this.props.item.return_types.map((t) => t.value),
                        this.props.returnTypesList
                      )
                    : ""}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Investment Types</p>
                <p className="panel-info-value">
                  {this.props.item.investment_types
                    ? this.formatTypesByValue(
                        this.props.item.investment_types.map((t) => t.value),
                        this.props.investmentTypesList
                      )
                    : ""}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Max LTV / LTC</p>
                <p className="panel-info-value">
                  {formatPercentage(this.props.item.ltv_max)}
                </p>
              </Col>
            </Row>
            <Row>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Min IRR</p>
                <p className="panel-info-value">
                  {formatPercentage(this.props.item.min_irr)}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Min Investment</p>
                <p className="panel-info-value">
                  {formatCurrency(this.props.item.investment_min)}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Max Investment</p>
                <p className="panel-info-value">
                  {formatCurrency(this.props.item.investment_max)}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Min Hold Period</p>
                <p className="panel-info-value">
                  {parseFloat(this.props.item.holding_period_min)
                    ? +this.props.item.holding_period_min + " Years"
                    : "N/A"}
                </p>
              </Col>
            </Row>
            <Row>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Max Hold Period</p>
                <p className="panel-info-value">
                  {parseFloat(this.props.item.holding_period_max)
                    ? +this.props.item.holding_period_max + " Years"
                    : "N/A"}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Min Rate</p>
                <p className="panel-info-value">
                  {formatPercentage(this.props.item.rate_min)}
                </p>
              </Col>
              <Col md="3" sm="6">
                <p className="small-title deal-info-title">Max Rate</p>
                <p className="panel-info-value">
                  {formatPercentage(this.props.item.rate_max)}
                </p>
              </Col>
            </Row>
            <Divider />
            <div className="ic-panel-details">
              <div className="flex">
                <p className="small-title">Property Types</p>
                <p>
                  {this.props.item.property_types
                    ? this.formatTypesByValue(
                        this.props.item.property_types.map((t) => t.value),
                        this.props.propTypesList
                      )
                    : ""}
                </p>
              </div>
              <div
                className={
                  this.props.item.property_types.find((p) => p.value === 7)
                    ? "flex"
                    : "hidden"
                }
              >
                <p className="small-title">Construction</p>
                <p>
                  {this.props.item.construction_types
                    ? this.formatTypesByValue(
                        this.props.item.construction_types.map((t) => t.value),
                        this.props.constructionTypesList
                      )
                    : ""}
                </p>
              </div>
              <div className="flex">
                <p className="small-title">Location</p>
                <p>
                  {this.props.item.nationwide
                    ? "Nationwide"
                    : this.props.item.states
                    ? this.formatTypesByValue(
                        this.props.item.states.map((t) => t.value),
                        this.props.statesList
                      )
                    : ""}
                </p>
              </div>
              <div className="flex">
                <p className="small-title">Does Investor Need Cash Flow</p>
                <div className="flex">
                  <div className="rkmd-checkbox checkbox-ripple">
                    <label className="input-checkbox checkbox-lightBlue">
                      <input
                        type="checkbox"
                        disabled
                        id="checkbox-1"
                        checked={this.props.item.cash_flow_needed}
                        onChange={() => {}}
                      />
                      <span className="checkbox"></span>
                      <span className="ripple"></span>
                    </label>
                    <p htmlFor="checkbox-1" className="label">
                      Cash Flowing Deal
                    </p>
                  </div>
                </div>
              </div>
            </div>
            {/*<Divider />*/}
            {/*<div>*/}
            {/*<p className="small-title">Notes</p>*/}
            {/*<p className="note">{this.props.item.note || 'N/A'}</p>*/}
            {/*</div>*/}
          </div>
        </div>

        <ConfirmationModal
          isOpen={this.state.showDeleteModal}
          message="Are you sure you would like to delete this investing criteria?"
          close={() => this.setState({ showDeleteModal: false })}
          confirm={() => this.delete()}
        />
      </div>
    );
  }
}

export default InvestingCriteria;
