import React from "react";
import { connect } from "react-redux";
import Modal from "react-modal";
import { bindActionCreators } from "redux";
import * as investingCriteriaActions from "../../actions/investingCriteriaActions";
import * as lendingCriteriaActions from "../../actions/lendingCriteriaActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import InvestingCriteriaPage from "./InvestingCriteriaPage";
import Expire from "../common/Expire";
import Button from "muicss/lib/react/button";
import * as compsActions from "../../actions/compsActions";
import getParameterByName from "../common/getParameterByName";

class InvestingCriteriaContainer extends React.Component {
  async componentDidMount() {
    this.props.navActions.updateNavTitle("Investing Criteria");
    this.props.actions.getFundTypes();
    this.props.actions.getReturnTypes();
    this.props.actions.getInvestmentTypes();
    this.props.actions.getConstructionTypes();
    this.props.lendingCriteriaActions.getPropertyTypes();
    this.props.lendingCriteriaActions.getStateList();
    await this.getInvestingCriteria();

    if (
      this.props.comps.previousScrollPos &&
      this.props.comps.previousScrollPos.length
    ) {
      window.scrollTo(
        0,
        this.props.comps.previousScrollPos[
          this.props.comps.previousScrollPos.length - 1
        ]
      );
    }
    this.props.compsActions.resetScrollPosition();
  }

  async UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.props.location.search !== nextProps.location.search) {
      await this.getInvestingCriteria();
    }
  }

  async getInvestingCriteria() {
    await this.props.actions.getInvestingCriteriaList(
      getParameterByName("page") || 1
    );
  }

  render() {
    let toast = null;
    if (
      this.props.investingCriteria.toast &&
      this.props.investingCriteria.toast.show
    ) {
      toast = (
        <Expire closeToast={this.props.actions.closeToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.investingCriteria.toast.message}</p>
            <Button variant="flat" onClick={this.props.actions.closeToast}>
              {this.props.investingCriteria.success ? "Done" : "Dismiss"}
            </Button>
          </div>
        </Expire>
      );
    }
    return (
      <div>
        {!this.props.investingCriteria.loading &&
        this.props.investingCriteria.fundTypesList.length &&
        this.props.investingCriteria.returnTypesList.length &&
        this.props.investingCriteria.investmentTypesList.length &&
        this.props.investingCriteria.constructionTypesList.length &&
        this.props.lendingCriteria.propTypesList.length &&
        this.props.lendingCriteria.stateList.length ? (
          <div>
            <InvestingCriteriaPage
              investingCriteriaList={
                this.props.investingCriteria.investingCriteriaList.data
              }
              currentPage={getParameterByName("page") || 1}
              from={this.props.investingCriteria.investingCriteriaList.from}
              nextPageUrl={
                this.props.investingCriteria.investingCriteriaList.next_page_url
              }
              fundTypesList={this.props.investingCriteria.fundTypesList}
              returnTypesList={this.props.investingCriteria.returnTypesList}
              investmentTypesList={
                this.props.investingCriteria.investmentTypesList
              }
              propTypesList={this.props.lendingCriteria.propTypesList}
              constructionTypesList={
                this.props.investingCriteria.constructionTypesList
              }
              statesList={this.props.lendingCriteria.stateList
                .slice(1)
                .map((s) => ({ list_type_id: s.id, value: s.state }))}
              delete={this.props.actions.deleteInvestingCriteria}
              setPageAndScrollPos={this.props.compsActions.setPageAndScrollPos}
            />
            {toast}
          </div>
        ) : (
          <Modal
            className="loading-modal"
            isOpen={true}
            ariaHideApp={false}
            contentLabel="Modal"
            shouldCloseOnOverlayClick={false}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                alt="spin"
                className="loading-spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    investingCriteria: state.investingCriteria,
    lendingCriteria: state.lendingCriteria,
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(investingCriteriaActions, dispatch),
    lendingCriteriaActions: bindActionCreators(
      lendingCriteriaActions,
      dispatch
    ),
    compsActions: bindActionCreators(compsActions, dispatch),
    navActions: bindActionCreators(navigationTitleActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(InvestingCriteriaContainer);
